const Countdown = () => {
  const calculateTimeLeft = () => {
    const targetDate = new Date("2025-06-29T23:59:59");
    const now = new Date();
    const difference = targetDate - now;

    if (difference <= 0) return null;

    const days = Math.floor(difference / (1000 * 60 * 60 * 24));
    const hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
    const minutes = Math.floor((difference / (1000 * 60)) % 60);
    const seconds = Math.floor((difference / 1000) % 60);

    return { days, hours, minutes, seconds };
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  useEffect(() => {
    const timer = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);
    return () => clearInterval(timer);
  }, []);

  if (!timeLeft) {
    return <p className="mt-4 text-red-600 font-semibold">Aplicaciones cerradas!</p>;
  }

  return (
    <div className="mt-6 text-center">
      <br/>
      <br/>
      <p className="text-lg font-semibold">Fecha lmite del semestre actual: 29 de Junio 2025.</p>
      <h2 className="text-lg font-semibold">Tiempo restante para enviar tu aplicativo terminado:</h2>
      <h2 className="text-xl font-mono">
        {timeLeft.days} das, {timeLeft.hours} horas, {timeLeft.minutes} minutos, {timeLeft.seconds} segundos
      </h2>
    </div>
  );
};

const Checklist1 = () => {
  const [checked, setChecked] = useState(Array(tasks.length).fill(false));

  const toggleCheck = (index) => {
    const newChecked = [...checked];
    newChecked[index] = !newChecked[index];
    setChecked(newChecked);
  };

  return (
    <div className="p-4 max-w-xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Lista para mi proceso de aplicacin</h1>
      <ul className="space-y-4">
        {tasks.map((task, index) => (
          <li key={index} className="flex items-start space-x-3">
            <button onClick={() => toggleCheck(index)}
            className="focus:outline-none mr-2">
              {checked[index] ? (
                <CheckCircle className="text-green-500 w-6 h-6 flex-shrink-0" />
              ) : (
                <Circle className="text-gray-400 w-6 h-6 flex-shrink-0" />
              )}
            </button>
            <span className={`text-lg ${checked[index] ? "line-through text-gray-500" : ""}`}>
            {task}
            
          </span>
          </li>
        ))}
      </ul>
      <Countdown />
    </div>
  );
};

const Checklist = () => (
  <>
    <Checklist1 />
    <hr className="my-6" />
  </>
);

export default Checklist;